<template>
    <div>
        <h1>Hello World</h1>
        <div v-if="showCookieWarning" class="cookie-warning">
            Cookies are required for this feature to work properly. Please enable cookies in your browser settings.
        </div>
        <input type="file" accept=".csv" @change="handleFileUpload">
        <div v-if="error" class="error-message">
            {{ errorMessage }}
        </div>
    </div>
</template>

<script>
import { ref } from 'vue';
import { useMutation } from '@vue/apollo-composable';
import { gql } from '@apollo/client/core';
import Papa from 'papaparse'; // Import PapaParse library

// GraphQL mutation to add a lead to the Strapi database
const ADD_LEAD = gql`
    mutation AddLead($name: String!, $phoneNumber: String!, $email: String!, $status: String!, $occupation: String!, $project: String!, $source: String!) {
      createLead(data: { name: $name, phoneNumber: $phoneNumber, email: $email, status:$status,occupation: $occupation, project: $project, source: $source }) {
        data {
          id
          attributes {
            name
            phoneNumber
            email
            occupation
            project
            source
            status
          }
        }
      }
    }
  `;

export default {
    name: 'HelloWorld',
    props: {
        projectKey: {
            type: String,
            required: true
        }
    },
    setup(props) {
        const showCookieWarning = ref(false);
        const error = ref(false);
        const errorMessage = ref('');

        const { mutate: addLead } = useMutation(ADD_LEAD);

        // Function to handle CSV file upload
        const handleFileUpload = async (event) => {
            const file = event.target.files[0];
            if (!file) return;

            try {
                const csvData = await parseCSV(file);
                await processCSVData(csvData);
            } catch (err) {
                error.value = true;
                errorMessage.value = `Error uploading CSV: ${err.message}`;
                console.error('Error uploading CSV:', err);
            }
        };

        // Function to parse CSV data into an array of objects using PapaParse
        const parseCSV = async (file) => {
            return new Promise((resolve, reject) => {
                Papa.parse(file, {
                    header: true,
                    skipEmptyLines: true,
                    complete: (results) => {
                        resolve(results.data);
                    },
                    error: (err) => {
                        reject(err);
                    }
                });
            });
        };

        // Function to process CSV data and add leads
        const processCSVData = async (csvData) => {
            for (let i = 0; i < csvData.length; i++) {
                const { name, phoneNumber, email, occupation, source, status } = csvData[i];
                try {
                    await addLead({
                        name,
                        phoneNumber,
                        email,
                        status,
                        occupation,
                        project: props.projectKey,
                        source
                    });
                    console.log(`Lead ${i + 1} added successfully.`);
                } catch (err) {
                    console.error(`Error adding lead ${i + 1}:`, err);
                }
            }
        };

        return {
            showCookieWarning,
            error,
            errorMessage,
            handleFileUpload,
        };
    },
};
</script>

<style scoped>
h1 {
    text-align: center;
    color: #333;
}

.cookie-warning {
    text-align: center;
    color: red;
    margin-top: 20px;
}

.error-message {
    text-align: center;
    color: red;
    margin-top: 20px;
}
</style>