<template>
    <div>
      <button @click="captureNumbers">Capture WhatsApp Numbers</button>
      <textarea v-model="capturedNumbers" rows="10" cols="50"></textarea>
    </div>
  </template>
  
  <script>
  export default {
    name:"WhatApp2",
    data() {
      return {
        capturedNumbers: ''
      };
    },
    methods: {
      captureNumbers() {
        // This function should be executed in the context of WhatsApp Web
        const script = `
          (function() {
            let contacts = [];
            let chatElements = document.querySelectorAll("._3j7s9");
            chatElements.forEach(chat => {
              let contact = chat.querySelector("._3ko75").textContent;
              contacts.push(contact);
            });
            return contacts;
          })()
        `;
        
        // Inject the script into the WhatsApp Web page and capture the result
        this.injectScript(script).then(contacts => {
          this.capturedNumbers = contacts.join('\\n');
        }).catch(error => {
          console.error('Error capturing numbers:', error);
        });
      },
      injectScript(scriptContent) {
        return new Promise((resolve, reject) => {
          // Create a script element
          const script = document.createElement('script');
          script.type = 'text/javascript';
          script.text = scriptContent;
          
          // Append the script to the body and wait for it to load
          document.body.appendChild(script);
          
          // Capture the result from the script
          script.onload = () => {
            const result = window.eval(scriptContent);
            resolve(result);
            // Remove the script after execution
            document.body.removeChild(script);
          };
          
          script.onerror = () => {
            reject(new Error('Failed to inject script'));
          };
        });
      }
    }
  };
  </script>
  
  <style scoped>
  /* Add any desired styles here */
  </style>
  