<template>
    <div>
        <h2>Send WhatsApp email</h2>
        <pre>{{ enDate }}</pre>
        <p>{{ projectKey }}</p>

        <div>
            <label for="phoneNumber">Telephone Number:</label>
            <input type="text" id="phoneNumber" v-model="phoneNumber">
        </div>
        <div>
            <label for="whatsappemail">WhatsApp email:</label>
            <textarea id="whatsappemail" v-model="whatsappemail"></textarea>
        </div>
        <button @click="sendemail">Send WhatsApp email</button>
    </div>
    <div>
        <div>
            <h3>{{ currentContact.name }}</h3>
            <p>{{ currentContact.phoneNumber }}</p>
            <p>{{ currentContact.email }}</p>
            <button @click="sendWhatsAppemail">Send via WhatsApp</button>
            <button @click="startProcessing">Start</button>
            <button @click="reset">Reset</button>
            <input type="file" @change="loadCSVFile" accept=".csv">
            <button @click="exportToCSV">Export to CSV</button>
            <div>
                <label for="jumpIndex">Enter Index:</label>
                <input type="number" id="jumpIndex" v-model="jumpIndex">
                <button @click="jumpToIndex">Jump</button>
            </div>
            <p v-if="error" class="error">{{ error }}</p>
        </div>

        <h2>Contacts List</h2>
        <table>
            <thead>
                <tr>
                    <th>No</th>
                    <th>Project</th>
                    <th>Source</th>
                    <th>Name</th>
                    <th>Phone Number</th>
                    <th>Occupation</th>
                    <th>email</th>
                    <th>Status</th>
                    <th>Date</th>
                    <th>NA1</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(contact, index) in contacts" :key="index" :class="{ 'selected': currentIndex === index }">

                    <td>{{ index + 1 }}</td> <!-- Display index number -->
                    <td>{{ contact.project }}</td>
                    <td>{{ contact.source }}</td>
                    <td>{{ contact.name }}</td>

                    <td>{{ contact.phoneNumber }}</td>
                    <td>{{ contact.occupation }}</td>
                    <td>{{ contact.email }}</td>
                    <td>{{ contact.status }}</td>
                    <td>{{ contact.date }}</td>
                    <td>{{ contact.na1 }}</td>
                </tr>
            </tbody>
        </table>

    </div>
</template>

<script>
// import emailEn from './emailEn.json';
// import emailCn from './emailCn.json';
// import emailBs from './emailBs.json';


import Papa from 'papaparse';

export default {
    name: 'ContactList',
    props: {
        projectKey: {
            type: String,
            required: true
        },
        enDate: {
            type: String,
            required: true
        }
    },
    components: {
    },
    mounted() {
        this.loademailFiles();
    },
    data() {
        return {
            emailEn: [],
            emailCn: [],
            emailBs: [],
            phoneNumber: '',
            whatsappemail: '',
            jumpIndex: null,

            currentIndex: 0,
            error: null,
            contacts: [
                { name: 'John', phoneNumber: '+601116331800', email: '!ei2', status: 'w', na1: "w" },
                { name: 'Jane', phoneNumber: '+601116331801', email: '!ei2', status: 'P', na1: "w" },
                { name: 'Alex', phoneNumber: '+601116331802', email: '!ei2', status: 'w', na1: "w" }
                // Add more contacts as needed
            ],
        };
    },
    computed: {
        currentContact() {
            return this.contacts[this.currentIndex] || {};
        }
    },
    methods: {
        formatString(text) {
            if (text === undefined) {
                return '';
            }

            // Remove all non-numeric characters
            text = text.replace(/\D/g, '');

            if (text.length <= 12) {
                return text.padEnd(12, '_'); // Pad with underscores if shorter
            } else {
                return text.slice(0, 12); // Truncate if longer
            }
        

    },
    loademailFiles() {
        this.loadTextFile('/messageEn.txt', 'emailEn');
        this.loadTextFile('/messageCn.txt', 'emailCn');
        this.loadTextFile('/messageBs.txt', 'emailBs');
    },
    loadTextFile(filePath, propertyName) {
        fetch(filePath)
            .then(response => response.text())
            .then(data => {
                console.log(`File loaded successfully: ${filePath}`);
                console.log(`Data: ${data}`);
                const lines = data.split('\n');
                const dataArray = [];
                let currentObject = { code: '', msg: '' };


                lines.forEach(line => {
                    if (!line.startsWith('_')) {

                        if (line.startsWith('@')) {

                            if (currentObject.code !== '') {
                                dataArray.push(currentObject);
                            }
                            const [code, msg] = line.split('|');
                            currentObject = { code: code.trim().substring(1), msg: msg.trim() + "\n" };
                        } else {
                            //      currentObject.msg += line.trim().replaceAll("$plink", "https://alvintkb.github.io/?p=gyp" + this.enDate + this.projectKey) + "\n";
                            currentObject.msg += line.trim().replaceAll("$plink", "https://alvintkb.github.io/?p=gyp" + this.enDate + "$phone" + this.projectKey) + "\n";

                        }
                    }
                });

                if (currentObject.code !== '') {

                    dataArray.push(currentObject);
                }

                this[propertyName].splice(0, this[propertyName].length, ...dataArray);
            })
            .catch(error => {
                console.error('Error loading text file:', error);
            });
    },
    sendemail() {
        if (this.phoneNumber && this.whatsappemail) {
            let msg = this.getemail(this.whatsappemail);
            //    navigator.clipboard.writeText(msg);

            // Create a temporary textarea element to hold the text
            const textarea = document.createElement('textarea');
            textarea.value = msg;
            document.body.appendChild(textarea);

            // Select and copy the text
            textarea.select();
            document.execCommand('copy');

            // Remove the temporary textarea
            document.body.removeChild(textarea);


            const cleanedPhoneNumber = this.cleanPhoneNumber(this.phoneNumber);
            const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
            window.open(url, '_blank');
        } else {
            alert('Please enter both telephone number and WhatsApp email.');
        }
    },
    jumpToIndex() {
        if (this.jumpIndex >= 0 && this.jumpIndex < this.contacts.length) {
            this.currentIndex = this.jumpIndex - 1;
        } else {
            console.error("Invalid index.");
        }
    },
    sendWhatsAppemail() {
        const { phoneNumber, email, na1, name } = this.currentContact;
        if (na1 !== 'w') {
            this.moveToNextContact();
            return;
        }
        if (this.validateInput(na1)) {
            let msg = this.getemail(email);
            msg = msg.replaceAll("@name", name);
            msg = msg.replaceAll("$phone", this.formatString(this.contacts[this.currentIndex].phoneNumber));
            navigator.clipboard.writeText(msg);
            const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);
            const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;

            const textarea = document.createElement('textarea');
            textarea.value = msg;
            document.body.appendChild(textarea);
            textarea.select();
            textarea.setSelectionRange(0, textarea.value.length);
            document.execCommand('copy');
            document.body.removeChild(textarea);

            window.open(url, '_blank');

            this.moveToNextContact();
        }
    },
    getemail(email) {
        let msg = "";

        if (email.slice(0, 2) === "!c")
            msg = this.emailCn.find(msg => msg.code === email.slice(1)).msg;
        else if (email.slice(0, 2) === "!b")
            msg = this.emailBs.find(msg => msg.code === email.slice(1)).msg;
        else if (email.slice(0, 2) === "!e")
            msg = this.emailEn.find(msg => msg.code === email.slice(1)).msg;
        else {
            alert("email error on 1st character, only allow e c b:" + email);
        }
        return msg;
    },
    validateInput(email) {
        if (!email) {
            this.error = 'Please enter a email.';
            return false;
        }
        this.error = null;
        return true;
    },
    moveToNextContact() {
        let checkedContacts = 0;
        do {
            this.currentIndex++;
            if (this.currentIndex >= this.contacts.length) {
                this.currentIndex = 0;
                checkedContacts++;
                if (checkedContacts >= this.contacts.length) {
                    break;
                }
            }
        } while (this.contacts[this.currentIndex].na1 !== 'w');
    },


    startProcessing() {
        this.currentIndex = 0;
        let checkedContacts = 0;
        while (this.contacts[this.currentIndex].na1 !== 'w') {
            this.moveToNextContact();
            checkedContacts++;
            if (checkedContacts >= this.contacts.length) {
                break;
            }
        }
    },
    reset() {
        this.currentIndex = 0;
    },
    loadCSVFile(event) {
        const file = event.target.files[0];
        if (!file) {
            this.error = 'Please select a file.';
            return;
        }
        Papa.parse(file, {
            header: true,
            complete: (results) => {
                this.contacts = results.data.map(contact => ({
                    name: contact.name ? contact.name.trim() : '',
                    phoneNumber: contact.phoneNumber ? contact.phoneNumber.trim() : '',
                    email: contact.email ? contact.email.trim() : '',
                    status: contact.status ? contact.status.trim() : '',
                    date: contact.date ? contact.date.trim() : '',
                    project: contact.project ? contact.project.trim() : '',
                    source: contact.source ? contact.source.trim() : '',
                    occupation: contact.occupation ? contact.occupation.trim() : '',
                    na1: contact.na1 ? contact.na1.trim() : ''
                }));
            },
            error: (error) => {
                console.error('Error parsing CSV file:', error);
                this.error = 'Error parsing CSV file.';
            }
        });
    },
    exportToCSV() {
        const csvContent = this.contacts.map(contact => {
            return `${contact.name},${contact.phoneNumber},${contact.email},${contact.status},${contact.date},${contact.na1}`;
        }).join('\n');

        const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        const url = URL.createObjectURL(blob);
        link.setAttribute('href', url);
        link.setAttribute('download', 'contacts.csv');
        link.style.visibility = 'hidden';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    }
    
}
}
</script>

<style scoped>
table {
    width: 100%;
    border-collapse: collapse;
}

th,
td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    background-color: #f2f2f2;
}

.col-no {
    width: 50px;
}

.col-name {
    width: 150px;
}


.col-phone {
    width: 150px;
}

.col-email {
    width: 250px;
}

.col-status {
    width: 100px;
}

.col-date {
    width: 150px;
}

.col-na1 {
    width: 100px;
}
</style>
